import React, {PureComponent} from 'react'
import {
  Modal,
  Table,
  Button,
  message
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({...state.outWarehouseListWave}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {

    }
    this.columns = [{
      title: '商品编号',
      width: 150,
      dataIndex: 'productCode',
      key: 'productCode'
    }, {
      title: '商品名称',
      width: 150,
      dataIndex: 'productName',
      key: 'productName'
    }, {
      title: '缺货总量',
      width: 100,
      dataIndex: 'shortageTotal',
      key: 'shortageTotal'
    }, {
      title: '单位',
      width: 100,
      dataIndex: 'unit',
      key: 'unit'
    }, {
      title: '订单编号',
      width: 150,
      dataIndex: 'outCode',
      key: 'outCode'
    }, {
      title: '配送地址',
      width: 150,
      dataIndex: 'deliveryAddress',
      key: 'deliveryAddress'
    }, {
      title: '缺货量',
      width: 100,
      dataIndex: 'shortageQty',
      key: 'shortageQty'
    }, {
      title: '拣选面',
      width: 150,
      dataIndex: 'storageLocationCode',
      key: 'storageLocationCode'
    }]
  }
  componentDidMount() {
    console.log(this.props, 11)
    this._getAsyncData()
  }
  // 根据tabs的key来判断请求哪个接口
  _getAsyncData() {
    const {handleGetStockOutSZO, modalId, reportModalType} = this.props
    switch (reportModalType) {
      case 1: handleGetStockOutSZO({searchParams: {waveCode: modalId}}, reportModalType); break
      case 2: handleGetStockOutSZO({searchParams: {orderCodeList: modalId}}, reportModalType); break
      default: break
    }
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const {update} = this.props
    update({isShowReportModal: false})
  }
  // 导出
  handleExport = () => {
    const {handleSocktOutExportSZO, searchParams, page, size, modalId, reportModalType} = this.props
    switch (reportModalType) {
      case 1: handleSocktOutExportSZO({searchParams: {page, size, waveCode: modalId, ...searchParams}}, () => {
        message.success('导出成功')
      }, reportModalType); break
      case 2: handleSocktOutExportSZO({searchParams: {page, size, orderCodeList: modalId, ...searchParams}}, () => {
        message.success('导出成功')
      }, reportModalType); break
      default: break
    }
  }
  // 出库
  handleOut = () => {
    const {handleSocktOutSZO, modalData} = this.props
    handleSocktOutSZO({urlParams: [modalData.id]}, () => {
      message.success('操作成功')
      const {handleGetList, searchParams, page, size} = this.props
      handleGetList({searchParams: {page, size, ...searchParams}})
      this.handleCloseModal()
    })
  }
  render() {
    const {deliveryTableLoading, searchResultRecord} = this.props
    return (
        <Modal
          title="出库缺货报表"
          visible={true}
          onCancel={this.handleCloseModal}
          footer={null}
          mask={false}
          width={777}
        >
          <Button type="primary" style={{marginRight: '16px'}} onClick={this.handleExport}>导出缺货明细</Button>
          {/* <Button type="primary" onClick={this.handleOut}>确认缺货出库</Button> */}
          <div className='table-group' style={{margin: 0}}>
            <Table
              rowKey={(record, index) => index}
              loading={deliveryTableLoading}
              columns={this.columns}
              scroll={{x: 'max-content', y: 400}}
              dataSource={searchResultRecord}
              pagination={false}
            />
          </div>
        </Modal>
    )
  }
}



