//异步

// 获取列表
export const handleGetList = (reqData, status, jump) => (dispatch) => {
  dispatch({type: actionType.OUTWAREHOUSELISTWAVE_UPDATE, data: {tableLoading: true}})
  let reqObjSearchParams = {...reqData.searchParams}
  if (reqObjSearchParams.initDate2) {
    reqObjSearchParams.planSendDate = moment(reqObjSearchParams.initDate2).format('YYYY-MM-DD')
  }
  delete reqObjSearchParams.initDate2
  for (let i in reqObjSearchParams) {
    if (Object.prototype.toString.call(reqObjSearchParams[i]) === '[object Object]') {
      reqObjSearchParams[i] = reqObjSearchParams[i].value
    }
  }
  if (reqObjSearchParams.initDate && reqObjSearchParams.initDate.length) {
    reqObjSearchParams.createTimeBegin = moment(reqObjSearchParams.initDate[0]).format('YYYY-MM-DD')
    reqObjSearchParams.createTimeEnd = moment(reqObjSearchParams.initDate[1]).format('YYYY-MM-DD')
  }
  delete reqObjSearchParams.initDate
  reqData.searchParams = reqObjSearchParams

  if (status) {
    getData({api: API_WAVES_PAGE, ...reqData}).then(data => {
      // 相应的逻辑判断
      let {page, size} = reqData.searchParams
      dispatch({
        type: actionType.OUTWAREHOUSELISTWAVE_UPDATE,
        data: {
          page,
          size,
          tableLoading: false,
          total: data.result.total,
          selectedRowKeys: [], // 选中的队列key
          selectedRows: [], // 选中的队列数据
          searchResult: data.result.list || []
        }
      })
    }).catch(err => {
      dispatch({
        type: actionType.OUTWAREHOUSELISTWAVE_FILE
      })
    })
  } else {
    // 查询统计数量tabs
    getData({api: API_WAVES_LOADCOUNTS, ...reqData}).then(({result}) => {
      // 相应的逻辑判断
      let arr = ['0', '1', '2', '3', '9']
      let status = ''
      let tabNumObj = {
        tab0: '',
        tab1: '',
        tab2: '',
        tab3: '',
        tab9: ''
      }
      result.map((o) => {
        tabNumObj[`tab${o.key}`] = o.value
      })
      if (jump) {
        reqData.searchParams.status = jump
        status = jump
      } else {
        for (let i = 0; i < arr.length; i++) {
          for (let j = 0; j < result.length; j++) {
            if (result[j].key == arr[i] && result[j].value) {
              status = arr[i]
              break
            }
          }
          if (status) break
        }
        if (status) {
          reqData.searchParams.status = status
        }
      }
      getData({api: API_WAVES_PAGE, ...reqData}).then(data => {
        // 相应的逻辑判断
        let {page, size} = reqData.searchParams
        dispatch({
          type: actionType.OUTWAREHOUSELISTWAVE_UPDATE,
          data: {
            page,
            size,
            tableLoading: false,
            total: data.result.total,
            tableActiveKey: status,
            selectedRowKeys: [], // 选中的队列key
            selectedRows: [], // 选中的队列数据
            searchResult: data.result.list || [],
            ...tabNumObj
          }
        })
      }).catch(err => {
        dispatch({
          type: actionType.OUTWAREHOUSELISTWAVE_FILE
        })
      })
    }).catch(err => {
      dispatch({
        type: actionType.OUTWAREHOUSELISTWAVE_FILE
      })
    })
  }
}

