// 获取波次详情的查询列表的结果
export const handleGetListDet = (reqData, headData) => (dispatch) => {
  dispatch({type: actionType.OUTWAREHOUSELISTWAVE_UPDATE, data: {deliveryTableLoading: true}})
  let reqObjSearchParams = {...reqData.searchParams}
  for (let i in reqObjSearchParams) {
    if (Object.prototype.toString.call(reqObjSearchParams[i]) === '[object Object]') {
      reqObjSearchParams[i] = reqObjSearchParams[i].value
    }
  }
  reqData.searchParams = reqObjSearchParams
  getData({api: API_WAVES_SELECTWAVEDETAILLIST, ...reqData}).then(data => {
    let modalData = headData
    headData.detailList = data.result || []
    // 相应的逻辑判断
    dispatch({
      type: actionType.OUTWAREHOUSELISTWAVE_UPDATE,
      data: {
        deliveryTableLoading: false,
        fullModalselectedRowKeys: [],
        fullModalselectedRows: [],
        modalData
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.OUTWAREHOUSELISTWAVE_FILE
    })
  })
}
