import React, {PureComponent} from 'react'
import {
  Row,
  Col,
  Table,
  Button,
  message,
  Input,
  Modal
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {CsxSelect, CsxMultipleSelect} from 'Form'
import {PICKSTATE, QYG, BBC} from 'Mock'
import ReportModal from './ReportModal'
import {API_WAVES_SHIPPEDORDERSTATUS} from 'api'
const confirm = Modal.confirm

@connect(
  state => ({...state.outWarehouseListWave, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: '出库单',
      width: 150,
      dataIndex: 'outCode',
      key: 'outCode',
      render: (text, record, index) => <a onClick={() => {
        const {modalData} = this.props
        let url = '/wms/out-warehouse/outWarehouseList/det/'
        if (QYG.indexOf(modalData.businessType) != -1) {
          url = '/wms/out-warehouse/outWarehouseList/DZdet/'
        }
        window.open(`${url}${record.outCode}`)
      }}>{text}</a>
    }, {
      title: '状态',
      width: 100,
      dataIndex: 'statusDesc',
      key: 'statusDesc'
    }, {
      title: '电子秤分拣状态',
      width: 100,
      dataIndex: 'dzcStatus',
      key: 'dzcStatus',
      render: (text, record, index) => <span className={text ? null : 'red'}>{text ? '已完成' : '未完成'}</span>
    }, {
      title: 'pda分拣状态',
      width: 100,
      dataIndex: 'pdaStatus',
      key: 'pdaStatus',
      render: (text, record, index) => <span className={text ? null : 'red'}>{text ? '已完成' : '未完成'}</span>
    }, {
      title: '创建时间',
      width: 150,
      dataIndex: 'createTime',
      key: 'createTime'
    }, {
      title: '分配短缺',
      width: 100,
      dataIndex: 'distributeShortageFlagDesc',
      key: 'distributeShortageFlagDesc'
    }, {
      title: '是否装车',
      width: 100,
      dataIndex: 'entruckingFlag',
      key: 'entruckingFlag',
      render: (text, record, index) => text ? Number(text) ? '是' : '否' : '否'
    }, {
      title: '门店',
      width: 150,
      dataIndex: 'shopCode',
      key: 'shopCode',
      render: (text, record, index) => `${record.shopCode} - ${record.shopName}`
    }, {
      title: '客户',
      width: 100,
      dataIndex: 'customerCode',
      key: 'customerCode',
      render: (text, record, index) => `${record.customerCode} - ${record.customerName}`
    }, {
      title: '站点',
      width: 100,
      dataIndex: 'stationCode',
      key: 'stationCode',
      render: (text, record, index) => `${record.stationCode} - ${record.stationName}`
    }]
  }
  componentDidMount() {
    this._getAsyncData()
  }
  // 根据tabs的key来判断请求哪个接口
  _getAsyncData() {
    const {handleTabDetail, match: {params}} = this.props
    handleTabDetail({urlParams: [params.id]})
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const {history} = this.props
    history.length > 1 ? history.goBack() : window.close()
  }

  //一键出库
  handleFullOut = () => {
    const {handleActionOut, modalData, handleTabDetail, match: {params}, fullModalselectedRows, update, handleShippedcheck} = this.props
    let data = [],
      orderCodeList = []
    fullModalselectedRows.map((o) => {
      data.push({orderCode: o.outCode, id: o.id})
      orderCodeList.push(o.outCode)
    })
    if (fullModalselectedRows.length) {
      // 增加BBC的出库逻辑
      if (BBC.indexOf(modalData.businessType) != -1) {
        handleShippedcheck({data: {orderCodeList}}, ({result}) => {
          switch (result.result) {
            case 1: handleActionOut({urlParams: [modalData.orderCode], data}, result => {
              handleTabDetail({urlParams: [params.id]}, true)
              message.info(`${result.message}`)
              if (result.isShortage) {
                update({isShowReportModal: true, modalId: result.failOrderCodeList, reportModalType: 2})
              }
            }); break
            case 0: update({orderCodeList, data, isShowBBCModal: true, BBCmessage: result.message});break
            case -1: message.error(result.message);break
            default: break
          }
        })
      } else {
        handleActionOut({urlParams: [modalData.orderCode], data}, result => {
          handleTabDetail({urlParams: [params.id]}, true)
          message.info(`${result.message}`)
          if (result.isShortage) {
            update({isShowReportModal: true, modalId: result.failOrderCodeList, reportModalType: 2})
          }
        })
      }
    } else {
      message.error('请选择需要出库的出库单')
    }
  }
  // 查询数据
  query = () => {
    const {handleGetListDet, searchParams2, modalData, match: {params}} = this.props
    let reqObj = {...searchParams2}
    handleGetListDet({searchParams: {waveId: params.id, ...reqObj}}, modalData)
  }
  // 重置
  reset = () => {
    const {update} = this.props
    update({searchParams2: {}})
  }
   // 改变当前的输入值
  onInputChange(values) {
    const {searchParams2, update} = this.props
    update({searchParams2: {...searchParams2, ...values}})
  }
  render() {
    const {modalData, deliveryTableLoading, fullModalselectedRowKeys, searchParams2: {dzcStatus, customerCode, pdaStatus, orderCode, orderStatus}, isShowBBCModal, update, handleWarnexport, orderCodeList, data, handleActionOut, handleTabDetail, match: {params}, BBCmessage} = this.props
    const rowSelection = {
      selectedRowKeys: fullModalselectedRowKeys,
      onChange: (fullModalselectedRowKeys, fullModalselectedRows) => {
        const {update} = this.props
        update({fullModalselectedRowKeys, fullModalselectedRows})
      },
      getCheckboxProps: record => ({
        disabled: record.status == 3 || !record.dzcStatus || !record.pdaStatus // Column configuration not to be checked
      })
    }
    return (
      <div className='fullOrderDel'>
        <Modal
          title="温馨提示"
          width="350px"
          visible={isShowBBCModal}
          footer={null}
          onOk={this.handleOk}
          onCancel={() => {
            update({isShowBBCModal: false})
          }}
        >
          <p style={{textAlign: 'center', color: 'red'}}>{BBCmessage}</p>
          <Row
            type="flex"
            justify="center"
          >
            <Col>
              <Button style={{marginRight: '48px'}} onClick={() => {
                update({isShowBBCModal: false})
                handleActionOut({urlParams: [modalData.orderCode], data}, result => {
                  handleTabDetail({urlParams: [params.id]}, true)
                  message.info(`${result.message}`)
                  if (result.isShortage) {
                    update({isShowReportModal: true, modalId: result.failOrderCodeList, reportModalType: 2})
                  }
                })
              }}>继续出库</Button>
              <Button type="primary" onClick={() => {
                update({isShowBBCModal: false})
                handleWarnexport({data: {orderCodeList}}, () => {
                  message.success('操作成功')
                })
              }}>下载异常订单</Button>
            </Col>
          </Row>
        </Modal>
        <span className='ant-modal-close-x close' onClick={this.handleCloseModal}></span>
        <div className='content'>
        {
          this.props.isShowReportModal ? <ReportModal /> : null
        }
          <div className='header' style={{fontSize: '14px'}}>
            <Row type="flex" justify="start">
              <Col span={24}>
                <Row type="flex" justify="start">
                  <Col span={6} style={{fontSize: '12px'}}>
                    <CsxSelect
                      data={PICKSTATE}
                      label='电子秤分拣状态'
                      selectItem={{data: dzcStatus, key: 'key', valueKey: 'value'}}
                      onChangeSelect={(value) => {
                        this.onInputChange({dzcStatus: value})
                      }}
                    />
                  </Col>
                  <Col span={6}>
                    <CsxSelect
                      data={PICKSTATE}
                      label='pda分拣状态'
                      selectItem={{data: pdaStatus, key: 'key', valueKey: 'value'}}
                      onChangeSelect={(value) => {
                        this.onInputChange({pdaStatus: value})
                      }}
                    />
                  </Col>
                  <Col span={6}>
                    <div className='commonInput'>
                      <label>客户编号：</label>
                      <div className='box'>
                        <Input placeholder='批量查询,请用逗号隔开' value={customerCode} onChange={(e) => {
                          this.onInputChange({customerCode: e.target.value})
                        }}/>
                      </div>
                    </div>
                  </Col>
                  <Col span={6}>
                    <div className='commonInput'>
                      <label>订单编号：</label>
                      <div className='box'>
                        <Input placeholder='批量查询,请用逗号隔开' value={orderCode} onChange={(e) => {
                          this.onInputChange({orderCode: e.target.value})
                        }}/>
                      </div>
                    </div>
                  </Col>
                  <Col span={6}>
                    <CsxMultipleSelect
                      dateApi={API_WAVES_SHIPPEDORDERSTATUS}
                      label='订单状态'
                      selectItem={{data: orderStatus, key: 'key', valueKey: ['key', 'value']}}
                      onChangeSelect={(value) => {
                        this.onInputChange({orderStatus: value})
                      }}
                    />
                  </Col>
                  <Col span={17}>
                    <Row type="flex" justify="end" >
                      <Button type="primary" style={{marginRight: '10px'}} onClick={this.query} >查询</Button>
                      <Button type="primary" onClick={this.reset}>重置</Button>
                    </Row>
                  </Col>
                  <Col span={24}>
                    {/* {
                      ['18', '20', '21', '22'].indexOf(modalData.businessType) != -1 && <Button type="primary" onClick={this.handleFullOut}>一键出库</Button>
                    } */}
                    <Button type="primary" onClick={this.handleFullOut}>一键出库</Button>
                  </Col>
                </Row>
              </Col>
              <Col span={6} className='delBox'>
                <label>波次号：</label>
                <span>{modalData.orderCode || ''}</span>
              </Col>
              <Col span={6} className='delBox'>
                <label>业务类型：</label>
                <span>{modalData.businessTypeDesc || ''}</span>
              </Col>
              <Col span={6} className='delBox'>
                <label>门店类型：</label>
                <span>{modalData.shopTypeDesc || ''}</span>
              </Col>
              <Col span={6} className='delBox'>
                <label>状态：</label>
                <span>{modalData.statusDesc || ''}</span>
              </Col>
              <Col span={6} className='delBox'>
                <label>创建时间：</label>
                <span>{modalData.createTime || ''}</span>
              </Col>
              <Col span={6} className='delBox'>
                <label>最新分配时间：</label>
                <span>{modalData.distributeTime || ''}</span>
              </Col>
              <Col span={6} className='delBox'>
                <label>全量分配：</label>
                <span>{modalData.allDistributeFlagDesc || ''}</span>
              </Col>
              <Col span={6} className='delBox'>
                <label>波次单打印次数：</label>
                <span>{modalData.wavePrintTimes || ''}</span>
              </Col>
              <Col span={6} className='delBox'>
                <label>计划发货日期：</label>
                <span>{modalData.planSendDate || ''}</span>
              </Col>
            </Row>
          </div>
          <div className='foot'>
            <div className='table-group'>
              <Table
                rowKey={record => record.id}
                loading={deliveryTableLoading}
                columns={this.columns}
                scroll={{x: 'max-content', y: 400}}
                dataSource={modalData.detailList}
                pagination={false}
                rowSelection={rowSelection}
              />
            </div>
          </div>
        </div>
      </div>
    )
  }
}



